import { Component, OnInit } from '@angular/core';
// import { devModeEqual } from '@angular/core/src/change_detection/change_detection';

@Component({
  selector: 'app-accelerometer',
  templateUrl: './accelerometer.component.html',
  styleUrls: ['./accelerometer.component.css']
})
export class AccelerometerComponent implements OnInit {
  x: number = 0
  y: number = 0
  isGyro: boolean = true;
  constructor() {

  }
  ngOnInit() {

    console.log('Engage');
    window.addEventListener('devicemotion', motion, false);


    let lastX, lastY, lastZ;
    let moveCounter = 0;

    function motion(e) {
      let acc = e.acceleration;
      if (!acc.hasOwnProperty('x')) {
        acc = e.accelerationIncludingGravity;
      }

      if (!acc.x) return;

      //only log if x,y,z > 1
      if (Math.abs(acc.x) >= 1 &&
        Math.abs(acc.y) >= 1 &&
        Math.abs(acc.z) >= 1) {
        //console.log('motion', acc);
        if (!lastX) {
          lastX = acc.x;
          lastY = acc.y;
          lastZ = acc.z;
          return;
        }

        let deltaX = Math.abs(acc.x - lastX);
        let deltaY = Math.abs(acc.y - lastY);
        let deltaZ = Math.abs(acc.z - lastZ);

        console.log(deltaX)
        console.log(deltaY)
        console.log(deltaZ)

        if (deltaX + deltaY + deltaZ > 3) {
          moveCounter++;
        } else {
          moveCounter = Math.max(0, --moveCounter);
        }

        if (moveCounter > 2) {
          console.log('SHAKE!!!');
          moveCounter = 0;
        }

        lastX = acc.x;
        lastY = acc.y;
        lastZ = acc.z;

      }
    }





    var deviceMotion = new DeviceMotionEvent("devicemotion")
    console.log(deviceMotion.acceleration.x)
    console.log(this.x)
    console.log(this.y)

    alert("hello")

    var x = 0;
    var gyroPresent = false;
    window.addEventListener("devicemotion", function (event) {
      console.log("event detected")
      x = event.acceleration.x;
      console.log(x)
      console.log(event.acceleration.y)
      if (event.rotationRate.alpha || event.rotationRate.beta || event.rotationRate.gamma) {
        gyroPresent = true;

      }
      console.log(gyroPresent);

    });
    this.isGyro = gyroPresent;
    this.x = x

  }




}
